@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
}
body {
  background-color: #8e8eff;
}

.App {
  position: relative;
  width: 100%;
  height: 100vh;
  padding: 50px 100px;
}

tr:hover {
  background-color: #00000040;
  cursor: pointer;
}
.CovidData {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: aliceblue;
  overflow: auto;
}
.CovidData table {
  border-collapse: collapse;
}
.CovidData h1 {
  width: 100%;
  background-color: rgb(15, 0, 82);
  padding: 20px;
  text-align: center;
  color: aliceblue;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.CovidData table {
  width: 100%;
}
.CovidData table thead tr th {
  padding: 20px;
  background-color: rgb(0, 255, 255);
  border: 2px solid #000;
}
.CovidData table tbody tr th {
  padding: 20px;
  border: 2px solid #000;
}

